
.info-container {
    width: 100%;
    height: 300px;
    background-color: white;
    color: black;
    margin-bottom: 100vh;
    
}

.info{
    padding: 100px 0 100px 0;
    font-size: 1.1vw;
    text-align: center;
    margin: 0 20px 0 20px;
}

.contenedor{
    width: 77%;
    margin: auto;
}

@media only screen and (max-width: 960px) {
    .info-container{
        height: 200px;
        width: 100%;
    }

    .info{
        padding: 10% 0;
        margin: auto;
        
        
    }


    .contenedor{
        width: 100%;
        margin: 0;
    }


    .contenedor > h3 {
        font-size: 15px;
    }

    .contenedor > p {
        padding-top: 5px;
        font-size: 15px;
    }

  }
